import React from "react";
import { Layout } from "antd";
import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";

import Nav from "./components/Nav";
import Home from "./components/Home";
import About from "./components/About";
import EtwasChrome from "./components/EtwasChrome";
import Sudo from "./components/Sudo";

const { Header, Content, Footer } = Layout;

function App() {
  return (
    <Router>
      <Layout basename={process.env.PUBLIC_URL}>
        <Header style={{ zIndex: 1, width: "100%", background: "#282a36" }}>
          <div className="logo" />
          <Nav />
        </Header>
        <Content className="content-container">
          <Route path="/" exact component={Home} />
          <Route path="/etwas" exact component={EtwasChrome} />
          <Route path="/sudo" exact component={Sudo} />
          <Route path="/about" exact component={About} />
        </Content>

        <Footer>tilda studio ©2019 created by Jingcheng Chen</Footer>
      </Layout>
    </Router>
  );
}

export default App;
